"use client"

import Link from "next/link"
import { usePathname } from "next/navigation"
import { cn } from "@/lib/utils"
import { Milk, Home, Calendar, Wallet, History, User, LogOut, Settings } from "lucide-react"

interface SidebarProps {
    className?: string
}

const navItems = [
    { href: "/customer", label: "Dashboard", icon: Home },
    { href: "/customer/subscribe", label: "My Subscription", icon: Milk },
    { href: "/customer/pause", label: "Pause / Vacation", icon: Calendar },
    { href: "/customer/wallet", label: "Wallet & Transactions", icon: Wallet },
    { href: "/customer/history", label: "Delivery History", icon: History },
    { href: "/customer/profile", label: "Profile Settings", icon: Settings },
]

export function Sidebar({ className }: SidebarProps) {
    const pathname = usePathname()

    return (
        <aside className={cn("bg-white border-r border-gray-100 flex flex-col h-screen sticky top-0", className)}>
            {/* Brand */}
            <div className="h-20 flex items-center px-8 border-b border-gray-100">
                <Link href="/" className="flex items-center gap-3 group">
                    <div className="w-10 h-10 bg-primary rounded-xl flex items-center justify-center text-white font-bold text-xl shadow-md transition-transform group-hover:scale-105">
                        M
                    </div>
                    <span className="font-heading font-bold text-xl text-primary-900">
                        Maa Ilay
                    </span>
                </Link>
            </div>

            {/* Navigation */}
            <nav className="flex-1 px-4 py-8 space-y-2 overflow-y-auto">
                <p className="px-4 text-xs font-semibold text-gray-400 uppercase tracking-wider mb-4">
                    Menu
                </p>

                {navItems.map((item) => {
                    const isActive = pathname === item.href

                    return (
                        <Link
                            key={item.href}
                            href={item.href}
                            className={cn(
                                "flex items-center gap-3 px-4 py-3 rounded-xl transition-all duration-200 group",
                                isActive
                                    ? "bg-primary-50 text-primary font-semibold"
                                    : "text-gray-600 hover:bg-gray-50 hover:text-gray-900"
                            )}
                        >
                            <item.icon className={cn(
                                "w-5 h-5 transition-colors",
                                isActive ? "text-primary" : "text-gray-400 group-hover:text-gray-600"
                            )} />
                            <span>{item.label}</span>
                            {isActive && (
                                <div className="ml-auto w-1.5 h-1.5 rounded-full bg-primary" />
                            )}
                        </Link>
                    )
                })}
            </nav>

            {/* Footer / User Info could go here */}
            <div className="p-4 border-t border-gray-100">
                {/* Logout or simple message */}
                <Link
                    href="/api/auth/signout"
                    className="flex items-center gap-3 px-4 py-3 rounded-xl text-red-500 hover:bg-red-50 hover:text-red-600 transition-colors"
                >
                    <LogOut className="w-5 h-5" />
                    <span className="font-medium">Sign Out</span>
                </Link>
            </div>
        </aside>
    )
}
