"use client"

import Link from "next/link"
import { motion } from "framer-motion"
import { ArrowLeft } from "lucide-react"
import { cn } from "@/lib/utils"

interface AuthLayoutProps {
  children: React.ReactNode
  title: string
  subtitle: string
  icon?: React.ReactNode
  variant?: "customer" | "admin" | "delivery"
  showBackButton?: boolean
}

export function AuthLayout({
  children,
  title,
  subtitle,
  icon,
  variant = "customer",
  showBackButton = true,
}: AuthLayoutProps) {
  // Theme logic based on variant
  // Customer: Pine Green (Primary)
  // Admin: Dark Slate (Default)
  // Delivery: Rich Brown or Orange? Let's stick to Primary for consistency for now, or use secondary.

  return (
    <div className="min-h-screen w-full flex items-center justify-center bg-primary relative overflow-hidden font-sans">

      {/* Abstract Background Shapes (Consistent with Landing Page) */}
      <div className="absolute top-0 right-0 w-[600px] h-[600px] bg-primary-light rounded-full blur-3xl opacity-20 translate-x-1/2 -translate-y-1/2" />
      <div className="absolute bottom-0 left-0 w-[500px] h-[500px] bg-secondary rounded-full blur-3xl opacity-10 -translate-x-1/2 translate-y-1/2" />

      {/* Back Button */}
      {showBackButton && (
        <Link
          href="/"
          className="absolute top-6 left-6 z-20 flex items-center gap-2 text-white/80 hover:text-white transition-colors"
        >
          <ArrowLeft className="w-5 h-5" />
          <span className="text-sm font-medium">Back to home</span>
        </Link>
      )}

      {/* Main Card */}
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
        className="relative z-10 w-full max-w-md mx-4"
      >
        <div className="bg-white rounded-3xl shadow-2xl overflow-hidden backdrop-blur-sm border border-white/20">

          {/* Header Section */}
          <div className={cn(
            "pt-10 pb-8 px-8 text-center",
            variant === "admin" ? "bg-slate-900" : "bg-white"
          )}>
            <motion.div
              initial={{ scale: 0.8 }}
              animate={{ scale: 1 }}
              className={cn(
                "w-16 h-16 rounded-2xl flex items-center justify-center shadow-lg mx-auto mb-6",
                variant === "admin"
                  ? "bg-slate-800 text-white"
                  : "bg-primary text-white"
              )}
            >
              {icon}
            </motion.div>

            <h1 className={cn(
              "text-2xl font-bold font-heading mb-2",
              variant === "admin" ? "text-white" : "text-primary"
            )}>
              {title}
            </h1>
            <p className={cn(
              "text-sm",
              variant === "admin" ? "text-slate-400" : "text-muted-foreground"
            )}>
              {subtitle}
            </p>
          </div>

          {/* Content Body */}
          <div className="px-8 pb-10 pt-2">
            {children}
          </div>
        </div>

        {/* Footer Note */}
        <p className="text-center mt-6 text-sm text-primary-100/60">
          &copy; {new Date().getFullYear()} Maa Ilay. Pure Milk, Pure Love.
        </p>

      </motion.div>
    </div>
  )
}
