"use client"

import { useState, Suspense } from "react"
import { signIn } from "next-auth/react"
import Link from "next/link"
import { useSearchParams } from "next/navigation"
import { Milk, Loader2 } from "lucide-react"
import { AuthLayout } from "@/components/layout"
import { Button } from "@/components/ui"

function LoginForm() {
  const [loading, setLoading] = useState(false)
  const searchParams = useSearchParams()
  const error = searchParams.get("error")
  const callbackUrl = searchParams.get("callbackUrl") || "/customer"

  const handleGoogleSignIn = async () => {
    setLoading(true)
    try {
      await signIn("google", { callbackUrl })
    } catch (err) {
      console.error("Sign in error:", err)
      setLoading(false)
    }
  }

  return (
    <>
      {/* Error Message */}
      {error && (
        <div className="mb-5 p-4 bg-red-50 border border-red-200 rounded-lg text-red-600 text-sm">
          {error === "OAuthSignin" && "Error starting sign in. Please try again."}
          {error === "OAuthCallback" && "Error during sign in. Please try again."}
          {error === "OAuthAccountNotLinked" && "This email is already linked to another account."}
          {!["OAuthSignin", "OAuthCallback", "OAuthAccountNotLinked"].includes(error) && "An error occurred. Please try again."}
        </div>
      )}

      {/* Google Sign In Button */}
      <Button
        onClick={handleGoogleSignIn}
        loading={loading}
        variant="outline"
        size="lg"
        className="w-full gap-3"
      >
        <svg className="w-5 h-5" viewBox="0 0 24 24">
          <path fill="#4285F4" d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z" />
          <path fill="#34A853" d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z" />
          <path fill="#FBBC05" d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l2.85-2.22.81-.62z" />
          <path fill="#EA4335" d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z" />
        </svg>
        Continue with Google
      </Button>

      {/* Terms */}
      <p className="mt-5 text-center text-xs text-[hsl(var(--muted-foreground))]">
        By signing in, you agree to our{" "}
        <Link href="/terms" className="text-[hsl(var(--primary))] hover:underline">Terms</Link>
        {" & "}
        <Link href="/privacy" className="text-[hsl(var(--primary))] hover:underline">Privacy Policy</Link>
      </p>

      {/* Info Note */}
      <div className="mt-5 p-4 bg-emerald-50 rounded-lg">
        <p className="text-xs text-emerald-700 leading-relaxed">
          <span className="font-semibold">New here?</span> Sign in with Google to create your account.
          Complete your profile and get admin approval to start your subscription.
        </p>
      </div>
    </>
  )
}

export default function CustomerLoginPage() {
  return (
    <AuthLayout
      title="Welcome to Maa Ilay"
      subtitle="Sign in to manage your milk subscription"
      icon={<Milk className="w-7 h-7 text-white" />}
      variant="customer"
    >
      <Suspense fallback={<div className="flex justify-center py-10"><Loader2 className="w-8 h-8 animate-spin text-primary" /></div>}>
        <LoginForm />
      </Suspense>
    </AuthLayout>
  )
}
